import { mapGetters, mapActions } from 'vuex';
import Dark from '../Icons/Dark.vue';
import Light from '../Icons/Light.vue';

export default {
  name: 'ThemeToggle',
  components: { Light, Dark },
  computed: mapGetters({
    theme: 'theme'
  }),
  mounted() {
    this.setTheme();
  },
  methods: mapActions({
    setTheme: 'setTheme'
  })
};
